import React, { Component } from "react";
// import axios from "axios";
import url from "../../strings/url";
import SignUpHeader from "../header/LoginHeader.js";

export default class Login extends Component {
  constructor(props) {
    super(props);
    this.state = {
      email: "",
      password: "",
      loginStatus: ""
    };
    this.onSignUpHandler = this.onSignUpHandler.bind(this);
    this.onChangeHandler = this.onChangeHandler.bind(this);
    this.onSubmitHandler = this.onSubmitHandler.bind(this);
  }

  onChangeHandler(event) {
    this.setState({ [event.target.name]: event.target.value });
    this.setState({ loginStatus: "" });
  }

  onBlurChangeHandler(event) {
    if (event.name === "passsword") {
    }
  }
  onSignUpHandler(event) {
    this.props.history.push("signup");
  }
  onSubmitHandler(event) {
    this.setState({ loginStatus: "loading" });

    const body = JSON.stringify({
      email: this.state.email,
      password: this.state.password
    });
    const headers = new Headers();
    headers.append("content-type", "application/json");
    const request = new Request(`${url}/user/login`, {
      method: "POST",
      headers,
      body
    });
    fetch(request)
      .then(async res => {
        if (res.status === 200) {
          const body = await res.json();
          this.setState({ loginStatus: "" });
          this.props.history.push("/home");
          localStorage.setItem("Anonimity#123Token", body.token);
        } else {
          this.setState({ loginStatus: "invalid" });
        }
      })
      .catch(e => {
        this.setState({ loginStatus: "unable" });
      });

    event.preventDefault();
  }

  render() {
    return (
      <div>
        <div style={{ display: "flex", justifyContent: "center" }}>
          <SignUpHeader
            classname={"comingFromRightP5"}
            heading="Anonimity(LogIn)"
          ></SignUpHeader>
        </div>
        <div
          style={{
            display: "flex",
            justifyContent: "center",
            marginBottom: 100
          }}
        >
          <form onSubmit={this.onSubmitHandler}>
            {this.state.loginStatus === "invalid" ? (
              <div
                className="notification comingFromLeftP5 is-danger"
                style={{ marginTop: 10 }}
              >
                Invalid Credentials
              </div>
            ) : null}
            <div
              className="notification comingFromLeft"
              style={{ background: "#ffffff" }}
            >
              <div className={"box comingFromUp"} style={{ marginLeft: 10 }}>
                <div className={"field"}>
                  <p className={"control has-icons-left has-icons-right"}>
                    <input
                      className="input"
                      type="email"
                      name="email"
                      placeholder="email"
                      value={this.state.email}
                      onChange={this.onChangeHandler}
                    ></input>
                    <span className="icon is-small is-left">
                      <i className="fas fa-envelope"></i>
                    </span>
                  </p>
                </div>
              </div>
              <div className={"box comingFromDown"} style={{ marginLeft: 10 }}>
                <div className={"field"}>
                  <p className={"control has-icons-left has-icons-right"}>
                    <input
                      className="input"
                      type="password"
                      name="password"
                      placeholder="Password"
                      value={this.state.password}
                      onChange={this.onChangeHandler}
                    ></input>
                    <span className="icon is-small is-left">
                      <i className="fas fa-lock"></i>
                    </span>
                  </p>
                </div>
              </div>

              <div
                className={"notification"}
                style={{
                  background: "#ffffff",
                  marginLeft: 10,
                  display: "flex",
                  justifyContent: "center",
                  flexDirection: "column"
                }}
              >
                <button
                  className={`button is-primary comingFromDown ${
                    this.state.loginStatus === "loading" ? "is-loading" : null
                  }`}
                  style={{ marginLeft: 10 }}
                  type="submit"
                >
                  Sign In
                </button>
                <h1
                  className={"comingFromLeft"}
                  style={{ marginTop: 10, fontWeight: "bolder" }}
                >
                  ------- OR -------
                </h1>
                <button
                  type="button"
                  className="button is-info comingFromUp"
                  style={{ marginTop: 10, marginLeft: 10 }}
                  onClick={this.onSignUpHandler}
                >
                  Sign Up
                </button>
              </div>
            </div>
          </form>
        </div>
      </div>
    );
  }
}
