import React from "react";
import SignUpHeader from "../../header/LoginHeader.js";

const Stage1 = props => {
  return (
    <div>
      <div style={{ display: "flex", justifyContent: "center" }}>
        <SignUpHeader classname={"comingFromRight"}></SignUpHeader>
      </div>
      <div
        style={{
          display: "flex",
          justifyContent: "center",
          marginBottom: 100
        }}
      >
        <form onSubmit={props.onSubmit}>
          <div
            className="notification comingFromUpP5"
            style={{ background: "#ffffff" }}
          >
            <div className={"box comingFromRightP5"} style={{ marginLeft: 10 }}>
              <div className={"field"}>
                <p className={"control has-icons-left has-icons-right"}>
                  <input
                    className="input"
                    type="email"
                    name="email"
                    placeholder="email"
                    value={props.email}
                    onChange={props.onChangeHandler}
                  ></input>
                  <span className="icon is-small is-left">
                    <i className="fas fa-envelope"></i>
                  </span>
                </p>
              </div>
            </div>

            <div className={"box comingFromLeftP5"} style={{ marginLeft: 10 }}>
              <div className={"field"}>
                <p className={"control has-icons-left has-icons-right"}>
                  <input
                    className="input"
                    type="password"
                    name="password"
                    placeholder="Password"
                    value={props.password}
                    onChange={props.onChangeHandler}
                  ></input>
                  <span className="icon is-small is-left">
                    <i className="fas fa-lock"></i>
                  </span>
                </p>
              </div>
            </div>

            <div className={"box comingFromRightP5"} style={{ marginLeft: 10 }}>
              <div className={"field"}>
                <p className={"control has-icons-left has-icons-right"}>
                  <input
                    className="input"
                    type="password"
                    name="confirmpassword"
                    value={props.confirmpassword}
                    onChange={props.onChangeHandler}
                    placeholder="Password (Re-Enter)"
                  ></input>
                  <span className="icon is-small is-left">
                    <i className="fab fa-rev"></i>
                  </span>
                </p>
              </div>
            </div>

            <div
              className={"notification"}
              style={{
                background: "#ffffff",
                marginLeft: 10,
                display: "flex",
                justifyContent: "center",
                flexDirection: "column"
              }}
            >
              <button
                className={`button is-primary comingFromLeftP5 ${
                  props.isLoading ? "is-loading" : null
                }`}
                style={{ marginLeft: 10 }}
                type="submit"
              >
                Next
              </button>
              <h1 style={{ marginTop: 10, fontWeight: "bolder" }}>
                ------- OR -------
              </h1>
              <button
                type="button"
                className="button is-info comingFromRightP5"
                style={{ marginTop: 10, marginLeft: 10 }}
                onClick={event => props.history.push("/login")}
              >
                Already have Account?
              </button>
            </div>
          </div>
        </form>
      </div>
    </div>
  );
};

export default Stage1;
