import React from "react";
import Header from "../../header/LoginHeader";
export default function(props) {
  return (
    <div className={"container is-fluid"}>
      <Header
        heading={"Not Authenticated"}
        classname={"comingFromDown"}
      ></Header>

      <button
        className={"button is-primary is-outlined comingFromUp"}
        style={{ marginTop: 100 }}
        onClick={e => {
          props.history.push("/login");
        }}
      >
        Go To LogIn Screen
      </button>
    </div>
  );
}
