import React, { Component } from "react";
import LoadingS from "./loadingScreen/LoadingS";
import NAS from "./NotAuthenticatedScreen/NAS";
import url from "../../strings/url";
import Home from "./Home/Home";
export default class AHome extends Component {
  constructor(props) {
    super(props);
    this.state = { AuthenticatedStatus: "loading", username: "" };
  }
  componentDidMount() {
    const headers = new Headers();
    headers.append("content-type", "application/json");
    headers.append("authorization", localStorage.getItem("Anonimity#123Token"));
    const request = new Request(`${url}/user/isAuth`, {
      method: "GET",
      headers
    });
    fetch(request)
      .then(r => {
        if (r.status === 200) {
          this.setState({ AuthenticatedStatus: "Auth" });
          r.json()
            .then(res => {
              this.setState({ username: res.username });
            })
            .catch(e => {
              console.log(e);
            });
        } else if (r.status === 401) {
          this.setState({ AuthenticatedStatus: "NotAuth" });
        }
      })
      .catch(e => {
        this.setState({ AuthenticatedStatus: "NotAuth" });
      });
  }

  render() {
    return (
      <div>
        {this.state.AuthenticatedStatus === "loading" ? <LoadingS /> : null}
        {this.state.AuthenticatedStatus === "NotAuth" ? (
          <NAS history={this.props.history} />
        ) : null}
        {this.state.AuthenticatedStatus === "Auth" ? (
          <Home username={this.state.username} />
        ) : null}
      </div>
    );
  }
}
