import React, { Component } from "react";

export default class Home extends Component {
  constructor(props) {
    super(props);
  }
  render() {
    return (
      <div>
        <h1 style={{ margin: 100 }} className={"comingFromUp"}>
          Hello {this.props.username}
          <br></br>
          Sorry For Inconvenience But This is to only Thing Our site has to
          offer (#indevelopment Stage)
        </h1>
        <button className="button is-danger comingFromDown">Log Out</button>
      </div>
    );
  }
}
